.button {
    width: 132px;
    height: 30px;
    border-radius: 3px;
    padding: 5px 24px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}

.button:disabled {

}

.disabled {
    color: black;
    background-color: #004D99;
    opacity: 50%;
}

.secondary {
    width: 176px;
    border: 1px solid #0066CC;
    color: #0066CC;
}

.secondary:active {
    width: 149px;
    background-color: #C9E5FF;
}

.secondary:hover {
    width: 167px;
    background-color: #E5F0FA;
}


.default {
    background-color: #0066CC;
}

.default:active {
    width: 149px;
    background-color: #0059B2;
}

.default:hover {
    width: 167px;
    background-color: #0080FF;
}


.red {
    background-color: #CC1439;
}

.red:active {
    width: 149px;
    background-color: #B21232;
    }

.red:hover {
    width: 167px;
    background-color: #FF1A47;
   }



